}

.movie-app > .row {
  overflow-x: auto;
  justify-content: center;
}

.image-container {
  position: relative;
  transition: transform 0.2s;
}

.image-container:hover {
  transform: scale(1.1);
}

.image-container:hover .overlay {
  opacity: 1;
}

.overlay {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  transition: 0.3s ease;
  opacity: 0;
  bottom: 0;
  font-size: 20px;
  padding: 10px;
  text-align: center;
  cursor: pointer;
}

.overlay:hover {
  background: #AF1B3F;
  border: 2px solid black;
}

.tag {
  align-items: center;
}

img {
  max-width: 270px;
}

.watchlist {
  font-family: 'Source Code Pro', monospace;
  background: rgba(0, 0, 0, 0.4);
  width: 96%;
  align-items: center;
  margin: 0 auto;
}

.row {
  overflow: hidden;
}
